/* ===== Sidebar Container ===== */
.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 70px; /* collapsed */
  background: #1a1a1a;
  padding: 15px 10px;
  display: flex;
  flex-direction: column;
  transition: width 0.25s ease;
  overflow: hidden;
  z-index: 999;
}

.sidebar.open {
  width: 230px; /* expanded */
}

/* ===== Logo Section ===== */
.sidebar-logo {
  display: flex;
  align-items: center;
  gap: 12px;
  padding-bottom: 25px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.08);
}

.sidebar-logo img {
  width: 50px;
  height: 50px;
  border-radius: 10px;
  object-fit: cover;
}

.sidebar-logo h2 {
  font-size: 1.2rem;
  color: white;
  font-weight: 600;
}

/* ===== Links Section ===== */
.sidebar-links {
  display: flex;
  flex-direction: column;
  gap: 22px;
  margin-top: 20px;
}

.section-title {
  font-size: 0.75rem;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.45);
  margin-left: 5px;
  letter-spacing: 1px;
}

/* ===== Each Link ===== */
.sidebar-links a {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 8px 12px;
  color: #ddd;
  text-decoration: none;
  font-size: 0.95rem;
  border-radius: 8px;
  transition: background 0.2s, color 0.2s;
}

.sidebar-links a:hover {
  background: rgba(255, 255, 255, 0.12);
  color: #fff;
}

/* Active Page */
.sidebar-links a.active {
  background: gold;
  color: white;
  font-weight: 600;
}

.sidebar-links svg {
  font-size: 22px;
}

/* ===== Prevent text shifting ===== */
.sidebar span {
  white-space: nowrap;
}

/* =================================================== */
/* ===== MAIN CONTENT FIX (THIS FIXES OVERLAPPING) ==== */
/* =================================================== */
.main-content {
  margin-left: 70px; /* sidebar collapsed width */
  padding: 20px;
  transition: margin-left 0.25s ease;
}

.sidebar.open ~ .main-content {
  margin-left: 230px; /* sidebar expanded width */
}

/* ===== Mobile Fix ===== */
@media (max-width: 768px) {
  .sidebar {
    width: 65px;
  }
  .sidebar.open {
    width: 200px;
  }

  .main-content {
    margin-left: 65px;
  }

  .sidebar.open ~ .main-content {
    margin-left: 200px;
  }
}
